.LabelValue {
  margin-bottom: 8px;
}

.LabelValue-medium {
  margin-bottom: 5px;
}

.LabelValue-value {
  font-weight: 700;
  font-size: 32px;
  line-height: 0.95;
  text-transform: uppercase;
}

.LabelValue-medium .LabelValue-value {
  font-size: 18px;
}

.LabelValue-label {
  text-transform: uppercase;
  font-weight: 300;
  font-size: 12px;
  display: block;
}

.LabelValue-label svg {
  margin-top: -2px;
  margin-right: 2px;
}



